/* Profile Page Container */
.profile-page {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 40px 20px;
    background-color: #f4f4f9;
    min-height: 100vh;
  }
  
  /* Profile Info Section */
    .profile-info {
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid #ddd;
    padding: 30px 40px;
    border-radius: 15px;
    background-color: #ffffff;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
    width: 100%;
    max-width: 500px;
    transition: transform 0.2s ease-in-out;
  }
  
  .profile-info:hover {
    transform: scale(1.05); /* Slight zoom on hover */
  }
  
  /* Avatar Section */
  .avatar {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    background-color: #007bff;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
    overflow: hidden;
    position: relative;
  }
  
  .avatar img {
    width: 100%;
    height: 100%;
    object-fit: cover; /* Ensures the image covers the circle without stretching */
  }
  
  .avatar-placeholder {
    font-size: 40px;
    color: white;
    font-weight: bold;
  }
  
  /* Heading and User Info Styling */
  h1 {
    font-size: 2.5rem;
    color: #333;
    margin-bottom: 20px;
  }
  
  h2 {
    font-size: 1.6rem;
    color: #007bff;
    margin-bottom: 10px;
  }
  
  p {
    font-size: 1rem;
    color: #555;
    margin-bottom: 20px;
  }
  
  /* Form Styling */
  form {
    width: 100%;
  }
  
  div {
    margin-bottom: 20px;
  }
  
  label {
    display: block;
    font-size: 1rem;
    color: #333;
    margin-bottom: 8px;
  }
  
  input {
    width: 100%;
    padding: 10px;
    font-size: 1rem;
    border: 1px solid #ddd;
    border-radius: 5px;
    box-sizing: border-box;
  }
  
  input:focus {
    border-color: #007bff;
    outline: none;
  }
  
  /* Button Styling */
  button {
    background-color: #ff0000;
    color: white;
    border: none;
    padding: 12px 20px;
    border-radius: 8px;
    cursor: pointer;
    font-size: 1rem;
    width: 100%;
    transition: background-color 0.3s ease;
  }
  
  button:hover {
    background-color: #0056b3;
  }
  
  button:disabled {
    background-color: #c0c0c0;
    cursor: not-allowed;
  }